body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home */
#home{
      background: rgb(0, 101, 219);
      background: linear-gradient(90deg, rgb(0, 101, 219) 50%, rgba(0, 212, 255, 1) 100%);
      min-height: 500px;
}

/* Cards */
.card{
  transition: all 0.5s ease;
}

.card:hover{
  cursor: pointer;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .15);
}

/* Form */
.form{
  background: rgb(0, 101, 219);
  background: linear-gradient(90deg, rgb(0, 101, 219) 30%, rgba(0, 212, 255, 1) 100%);
  min-height: 500px;
}


/*Footer*/

.footer{
  background: rgb(0, 101, 219);
  background: linear-gradient(90deg, rgb(0, 101, 219) 40%, rgba(0, 212, 255, 1) 100%);
  min-height: 500px;
}
